.hero-section{
    background-image: url(./images/bg-image.png);
    background-size: cover;
    height: 100vh;
    overflow: hidden;
    background-position-y: 50%;
    background-position-x: 30%;
}
.hero-h2{
    color: #f1f1f1;
    margin-left: auto;
    margin-right: auto; 
    text-align: center; 
    font-size: 2.2rem; 
    line-height: 1.3; 
} 
.p-hero{ 
    color: #f1f1f1; 
    width: 90%; 
    margin-left: auto; 
    margin-right: auto; 
    text-align: center; 
    font-size: 0.9rem; 
} 
.hero-text{ 
    margin: 1em; 
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
} 

@media screen and (min-width:950px) { 
    .p-hero{
        width: 45%;
    }
}

@media screen and (min-width:1850px) {
    .p-hero{
        width: 25%;
    }
}